@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

//Sass Variables
@import 'variables';

//Base css styles
@import 'base';

//Display flex and accompanying styles
@import 'layout';

//Utilities
@import 'utilities';

@import 'mediaQueries';


/* NAVBAR STYLES */
.menu-icon {
    background-color: white;
    width: 40px;
    height: 40px;
    z-index: 10001;
}

.mobile-menu h2 {
    z-index: 100004;
}

.navbar {
    z-index: 100003;
    background-image: linear-gradient(#303f2e, black);
}

.navbar h2 span {
    transform: rotate(20deg);
    top: -10px;
    color: #f94a1d;
}

.links a {
    display: inline-block;
    padding: 10px 20px 10px 20px;
    border-radius: 10px;
}

.links a:hover {
  background-color: $lightOrange;
}

/* HOME STYLES */
.home .w-40 {
    padding-top: 150px;
    padding-bottom: 150px;
}

.home span {
    color: $lightOrange;
}

.welcome {
    border: 1px solid #f94a1d;
}

.hire-me, 
.download-cv {
    background-color: $lightOrange;
    border: 2px solid $lightOrange;
    cursor: pointer;
}

.explore {
    border: 1px solid white;
}

.hire-me:hover,
.explore:hover {
    background-color: white;
    border-color: $lightOrange;
    color: $lightOrange;
    transition: all .3s ease-in-out;
}

.orange-square {
    width: 500px;
    height: 600px;
    background: #f94a1d;
    transform-origin: center;
    transform: translate(210px, -210px) rotate(45deg) ;
    z-index: -1;
}

.orange-feature {
    width: 500px;
    height: 500px;
    background: #f94a1d;
    border-radius: 100%;
}

.white-circle {
    width: 390px;
    height: 390px;
    background: white;
    border-radius: 100%;
}

.img-circle {
    width: 360px;
    height: 360px;
    background: white;
    border-radius: 100%;
    img {
        position: relative;
        transform: translateY(30px);
    }
}

/* ABOUT STYLES */
.about {
    padding-top: 100px;

    h2 {
        text-decoration: underline $lightOrange;
    }
}

.skill {
    width: 330px;
    background-color: white;
    cursor: pointer;
    filter: grayscale(1);

    .skill-img {
        background-color: white;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }

    .skill-text {
        background-color: white;
        border-bottom-left-radius: 35px;
        border-bottom-right-radius: 35px;

        p {
            border-top-left-radius: 35px;
            background-color: $lightOrange;
            bottom: -90px;
        }
    }
}

.skill-img:hover .skill-text {
    transition: all 1s ease-in-out;
    color: red;
}

.skill:hover {
    filter: grayscale(0);
    transition: all .5s ease-in-out;
}

.skill:hover .skill-text p {
    transition: all .5s ease-in-out;
    transform: translateY(-85px);
}

.latest-portfolio-code {
    width: 70%;
}

.latest-portfolio-code img {
    background-color: white;
}

.latest-portfolio-code h3 a{
    text-decoration: underline $lightOrange;
}

/* PROJECT STYLES */
.projects {
    padding-top: 80px;
}

/*Projects*/
.project-selector {
    flex-direction: column !important;
    justify-content: center ;
    text-align: center ;
} 

.view-code {
    background-color: $lightOrange;
    transform: rotate(-45deg);
    left: -40px;
    z-index: 100001;
}


.personal-projects {

    h2 {
        text-decoration: underline $lightOrange;
    }
}

.project-overlay {
    background-color: rgba($color: $black, $alpha: .6);
    height: 100%;
    top: 0;
    opacity: 0;
    cursor: pointer;
}

.project-overlay h3 {
    color: rgb(255, 255, 255);
    text-decoration: underline $lightOrange;
    height: fit-content;
}

.project-icons img {
    width: 50px;
}

.bootstrap-icon {
    background-color: white;
    height: 40px;
}

.then-and-now {

    h2 {
        text-decoration: underline $lightOrange;
    }
}

.oldPortfolio {
    max-width: 500px;
}

.card {
    width: 300px;
    height: 300px;
    margin: 5px;
}

.overlay {
    width: 100%;
    height: 100%;
    background-color: #252429;
    opacity: 0;
}

.overlay:hover {
    opacity: .8;
    transition: all .6s ease-in-out;
}

.card-title {
    text-decoration: underline $lightOrange;
}

/* contact */
.contact {
   padding-top: 100px;

   .title {
       text-decoration: underline $lightOrange;
   }
}

.contact-box {
    box-shadow: 0px 0px 15px 0px #000000;
    background-color: #252429;
    width: 300px;
}

.linkedIn-icon {
    background-color: white;
    border-radius: 9px;
    border: 1px solid white;
    cursor: pointer;
    filter: grayscale(1);
}

.linkedIn-icon:hover {
    transition: all .3s ease-in-out;
    filter: grayscale(0);
}

form {
    background-color: #252429;
    width: 550px;
    min-width: 400px;
    box-shadow: 0px 0px 15px 0px #000000;
}

.input-style {
    background: center;
    background-color: black;
    border: 0;
    border-bottom: 1px solid black;
    font-size: 16px;
}

.submission-overlay {
    height: 150vh;
    width: 101vw;
    left: -45px;
    z-index: 100005;
    background-color: rgba($color: #000000, $alpha: .7);
    div {
        background-color: aliceblue;
        height: 200px;
        width: 400px;
        h2 {
            color: black;
            text-decoration: underline $lightOrange;
        }

        span {
            color: $lightOrange;
        }
    }
}

/* footer */

.footer {
    background-image: linear-gradient(black, #303f2e);
}






